<template>
  <div class="wrap">
    <h1>MoviesPage</h1>

    <ul>
      <li v-bind="movie in movies" class="item" :key="movie.id">
        <b>Movie : </b><span>{{ movie.id }}</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  created() {
    this.$http.get('/api/movies')
      .then(res => {
        this.movies = res.data
      })
  },
  data() {
    return {
      movie: []
    }
  }
}
</script>